import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import MainIcon from '../images/main.jpg'
import 'font-awesome/css/font-awesome.min.css';
import { useRef } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper/modules";

import Img1 from '../images/projects/1.jpg'
import Img2 from '../images/projects/2.jpg'
import Img3 from '../images/projects/3.jpg'
import Img4 from '../images/projects/4.jpg'
import Img5 from '../images/projects/5.jpg'

import Foto1 from '../images/Fotografia-removebg-preview.png'
import Foto2 from '../images/gggg.png'
import Foto3 from '../images/molotok.png'
import Foto4 from '../images/molotok2.png'







import icon from '../images/icons/main-page-icons/advantages-1.svg'
import icon2 from '../images/icons/main-page-icons/advantages-2.svg'
import icon3 from '../images/icons/main-page-icons/advantages-3.svg'
import icon4 from '../images/icons/main-page-icons/advantages-4.svg'
import manufacture from '../images/icons/main-page-icons/manufacture-feature-1.svg'
import manufacture2 from '../images/icons/main-page-icons/manufacture-feature-2.svg'

// Стили слайдера 
import "./swiper.css";
import "./pagination.css";
import "./navigation.css";

import Header from "./header"
import "./layout.css"
import './bootstrap.css'
import './Responsive.css'
import "./litle-style.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  const progressCircle = useRef(null);
  const progressContent = useRef(null);
  const onAutoplayTimeLeft = (s, time, progress) => {
    progressCircle.current.style.setProperty("--progress", 1 - progress);
    progressContent.current.textContent = `${Math.ceil(time / 1000)}s`;
  };

  return (
    <>
      <div className="hero_area">
        <div className="hero_bg_box">
          <img src={MainIcon} alt=""></img>
        </div>
        <Header siteTitle={data.site.siteMetadata?.title || `Title`} />

        <section className="slider_section ">
          <div id="customCarousel1" className="carousel slide" data-ride="carousel">
            <div className="carousel-inner">
              <div className="carousel-item active">
                <div className="container ">
                  <div className="row">
                    <div className="col-lg-8 col-xl-6 mx-auto ">
                      <div className="iMac:mb-8 iMac:text-9xl font-serif font-bold text-white lg:text-7xl">
                        <h1 className="text">
                          ALTYN JULDYZ
                        </h1>
                        <p>
                          Группа компаний в Казахстане, вносящая огромный вклад в развитие разных отраслей страны
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      {/* COMFORT JAGALAU */}
      <section className="about_section layout_padding background-div">
        <div id="customCarousel1" className="carousel slide" data-ride="carousel">
          <div className="carousel-inner">
            <div className="carousel-item active">
              <div className="container ">
                <div className="row">
                  <div className="col-lg-8 col-xl-6 mx-auto ">
                    <div className="iMac:mb-8 iMac:text-9xl font-serif font-bold text-white lg:text-7xl">
                      <h3 className="2">
                        COMFORT JAGALAU
                      </h3>
                      <p>
                        Мы строим многофункциональный жилой комплекс в новом центре Алматинской области в городе Конаева. Для реализации этой цели мы собрали профессиональную команду в области дизайна, проектирования и строительства
                      </p>
                      <a href="#" className="custom-btn btn-14"> На саайт
                      </a>
                      <div>
                        <h2 className="top ">Наши преимущества</h2>
                      </div>
                    </div>
                    <div className="center-container">
                      <div className="icon-container">
                        <div className="icon">
                          <img src={icon} alt="Icon 3" />
                          <div className="icon-label">Камеры видеонаблюдения</div>
                        </div>
                        <div className="icon">
                          <img src={icon2} alt="Icon 3" />
                          <div className="icon-label">Подземный паркинг</div>
                        </div>
                      </div>

                      <div className="icon-container">
                        <div className="icon">
                          <img src={icon3} alt="Icon 3" />
                          <div className="icon-label">Детские и спортивные площадки</div>
                        </div>
                        <div className="icon">
                          <img src={icon4} alt="Icon 3" />
                          <div className="icon-label">Круглосуточная охрана и пропускная система</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Блок Швейно-обувная фабрика */}
      <section className="about_section layout_padding background-div2" id="my-anchor" >
        <div id="customCarousel1" className="carousel slide" data-ride="carousel">
          <div className="carousel-inner">
            <div className="carousel-item active">
              <div className="container ">
                <div className="row">
                  <div className="col-lg-8 col-xl-6 mx-auto ">
                    <div className="iMac:mb-8 iMac:text-9xl font-serif font-bold text-white lg:text-7xl">
                      <h1 className="2">
                        Швейно-обувная фабрика
                      </h1>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="flex">
          <div className="flex1">
            <img className="icon-fabrica" src={manufacture2}></img>
            <div className="text-fabrica">
              <h4>Индивидуальная разработка <br />дизайна спецодежды</h4>
              <p>Как производитель мы с удовольствием разработаем индивидуальный
                дизайн вашей спецодежды с учетом всех ваших пожеланий и
                с полным соблюдением фирменного стиля и цветовой гаммы вашей компании.</p>
            </div>

          </div>
          <div className="flex1">
            <img className="icon-fabrica" src={manufacture}></img>
            <div className="text-fabrica">
              <h4>Нанесение логотипа вашей компании</h4>
              <p>Мы нанесем логотип Вашей компании на изготавливаемую нами
                спецодежду или средства защиты (термопечатью, шелкографией или вышивкой).
                Это будень очень положительно влиять на имидж Вашей компании и на лояльность
                Ваших сотрудников и персонала.</p>
            </div>
          </div>

        </div>
        <div className="flex-oriintir">
          <a href="#" className="custom-btn btn-14"> На сайт </a>
        </div>

      </section>


      {/* Блок Средство индивидуальной защиты */}
      <section className=" layout_padding background-div3" id="my-anchor" >
        <div id="customCarousel1" className="carousel slide" data-ride="carousel">
          <div className="carousel-inner">
            <div className="text-white oriintir">
              <h1 className="text-top">
                Средство индивидуальной защиты
              </h1>
            </div>
            <div className="flex new-text">
              <p className="text-white text-gold"> ALTYN JULDYZ — надежные средства индивидуальной защиты
                Крупнейший производитель спецодежды и спецобуви в Казахстане
                Переходите в наш каталог, и сможете ознакомиться со
                стильной удобной спецодеждой и обувью, работать в которой легко, удобно,
                безопасно. Она не сковывает движений, обеспечивает оптимальный температурный режим,
                защищает уязвимые участки тела. Также у нас вы можете заказать средства защиты для рук,
                глаз, головы. Безопасность в работе — это не прихоть,
                а необходимость, и мы знаем, как ее обеспечить.</p>
              <div className="custom-flex-container">
                <div className="custom-flex-item">
                  <img className="size" src={Foto1}></img>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="flex-oriintir">
          <a href="#" className="custom-btn btn-14"> Перейти в каталог </a>
        </div>

      </section>
      {/* Строительные инструменты и материалы */}
      <section className=" layout_padding background-div4" id="my-anchor" >
        <div id="customCarousel1" className="carousel slide" data-ride="carousel">
          <div className="carousel-inner">
            <div className="text-white oriintir">
              <h1 className="text-top">
                Продажа Строительных инструментов и Материалов
              </h1>
            </div>
            <div>
              <p className="text-white text-gold2">ALTYN JULDYZ — надежные средства индивидуальной защиты
                Крупнейший производитель спецодежды и спецобуви в Казахстане
                Переходите в наш каталог, и сможете ознакомиться со
                стильной удобной спецодеждой и обувью, работать в которой легко, удобно,
                безопасно. Она не сковывает движений, обеспечивает оптимальный температурный режим,
                защищает уязвимые участки тела. Также у нас вы можете заказать средства защиты для рук,
                глаз, головы. Безопасность в работе — это не прихоть,
                а необходимость, и мы знаем, как ее обеспечить.</p>

            </div>
          </div>
        </div>
        <div className="custom-flex-container2">
          <div className="custom-flex-item">
            <img className="size" src={Foto3}></img>
          </div>
          <div className="custom-flex-item ">
            <img className="size" src={Foto2}></img>
          </div>
          <div className="custom-flex-item">
            <img className="size" src={Foto4}></img>
          </div>
        </div>
        <div className="flex-oriintir">
          <a href="#" className="custom-btn btn-14"> Перейти в каталог </a>
        </div>

      </section>


      {/* Строительное оборудование  */}
      <section className=" layout_padding background-div5" id="my-anchor" >
        <div id="customCarousel1" className="carousel slide" data-ride="carousel">
          <div className="carousel-inner">
            <div className="text-white oriintir">
              <h1 className="text-top">
                Строительное оборудование
              </h1>
            </div>
            <div className="flex3">
              <p className="text-white text-gold2">Группа компании «ALTYN JULDYZ» занимается
                продажей строительного оборудования применяемых в самых различных областях
                деятельности, поэтому для каждой из них подобная техника имеет свои индивидуальные
                отличия. Так, для дорожных работ используются виброкатки, трамбующая техника, нарезчики
                швов и прочие асфальт укладочные машины. А в сфере хозяйственного строительного
                оборудования главенствуют всевозможные тракторы, снегоуборочные устройства, мот
                культиваторы и т.п.</p>
              <div className="text-white text-gold2">
                <p className="gold1"><b>Виды оборудования:</b></p>
                <ul>
                  <li>Дорожное оборудование. Всевозможные виброкатки, асфальт укладчики и т.д.</li>
                  <li>Погрузочное-разгрузочные — это лебедки, тельферы, стропы, домкраты, которые различаются по способу управления и грузоподъемности.</li>
                  <li>Оборудование для земляных работ — это экскаваторы, скреперы, бульдозеры.</li>
                  <li>Инструменты для изготовления арматуры и проведения бетонных работ.</li>
                  <li>Бетономешалки, бензорезы и строительные тележки.</li>
                </ul>
              </div>

            </div>
          </div>
        </div>
        <div className="flex-oriintir">
          <a href="#" className="custom-btn btn-14"> Перейти в каталог </a>
        </div>
      </section>


      {/* Спортивные товары */}
      <section className=" layout_padding background-div6" id="my-anchor" >
        <div id="customCarousel1" className="carousel slide" data-ride="carousel">

          <div className=" carousel-inner flex5">
            <h2>Продажа Спортивных Товаров</h2>
          </div>
          <div className="carousel-inner flex4">
            <p>«GS SPORT» магазин спортивных товаров. Проект входит в группу компании «ALTYN JULDYZ» и имеет большой ассортимент в выборе товаров для спорта и дома.
              Наш спорт магазин для всей семьи! Все для спорта и активного отдыха – от самых простых спорт товаров до технологического оборудования последнего поколения.</p>
            <p>Высокое качество от популярного брэнда GS. Мы предлагаем вам большой выбор спортивных товаров. В каталоге представлены лучшие виды спортивного инвентаря с фотографиями и описанием. У нас вы можете купить: велосипеды для детей, роликовые коньки, кимоно для дзюдо и многое другое. Так же вы можете приобрести у нас мебель для вашего дома, зонты и шатры.</p>

          </div>
        </div>
        <div className="flex-oriintir">
          <a href="#" className="custom-btn btn-14"> Перейти в каталог </a>
        </div>
      </section>
    </>
  )
}

export default Layout
